import { ApiType, DataUtil, FileQueryParams, ProviderBase } from '@youfoundation/transit-lib';

export class FileProvider extends ProviderBase {
  private static instance: FileProvider;

  constructor(sharedSecret: Uint8Array) {
    super({ api: ApiType.Owner, sharedSecret: sharedSecret });
  }

  publishFile = async (fileName: string, sections: QueryParamsSection[]) => {
    const httpClient = this.createAxiosClient();

    const fileRequest: PublishStaticFileRequest = {
      filename: fileName,
      config: {
        crossOriginBehavior: 'allowAllOrigins',
        contentType: 'string',
      },
      sections: sections.map((section) => {
        return {
          ...section,
          queryParams: fixQueryParams(section.queryParams),
        };
      }),
    };

    return await httpClient.post('/optimization/cdn/publish', fileRequest);
  };

  public static getInstance(sharedSecret): FileProvider {
    if (!FileProvider.instance) {
      FileProvider.instance = new FileProvider(sharedSecret);
    }

    return FileProvider.instance;
  }
}

const fixQueryParams = (params: FileQueryParams): FileQueryParams => {
  params = { ...params };
  //HACK; convert all strings to byte arrays as base64 values; this is for a test

  //HACK: until we decide where to handle byte arrays
  if (params.tagsMatchAtLeastOne)
    params.tagsMatchAtLeastOne = params.tagsMatchAtLeastOne.map((v) =>
      DataUtil.uint8ArrayToBase64(DataUtil.stringToUint8Array(v))
    );
  if (params.tagsMatchAll)
    params.tagsMatchAll = params.tagsMatchAll.map((v) =>
      DataUtil.uint8ArrayToBase64(DataUtil.stringToUint8Array(v))
    );
  if (params.sender)
    params.sender = params.sender.map((v) =>
      DataUtil.uint8ArrayToBase64(DataUtil.stringToUint8Array(v))
    );

  return params;
};

type QueryParamsSection = {
  name: string;
  queryParams: FileQueryParams;
  resultOptions: {
    includeAdditionalThumbnails: boolean;
    includeJsonContent: boolean;
    includePayload: boolean;
    excludePreviewThumbnail: boolean;
  };
};

type PublishStaticFileRequest = {
  filename: string;
  config: { crossOriginBehavior: 'default' | 'allowAllOrigins'; contentType: string };
  sections: QueryParamsSection[];
};
