import { useQuery } from '@tanstack/react-query';
import { ApiType, Client, HomePageAttributes } from '@youfoundation/transit-lib';
import useAuth from '../auth/useAuth';

const useHomeAttributes = () => {
  const { getSharedSecret } = useAuth();
  const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });
  const fetchData = async () => {
    const foundHomettributes = (
      await client.homePageProvider.getAttributes(HomePageAttributes.HomePage.toString())
    ).map((attr) => {
      return {
        ...attr,
        typeDefinition: {
          type: HomePageAttributes.HomePage.toString(),
          name: 'Home Page',
          description: '',
        },
      };
    });

    const foundThemeAttributes = (
      await client.homePageProvider.getAttributes(HomePageAttributes.Theme.toString())
    ).map((attr) => {
      return {
        ...attr,
        typeDefinition: {
          type: HomePageAttributes.Theme.toString(),
          name: 'Theme',
          description: '',
        },
      };
    });

    return { home: foundHomettributes, theme: foundThemeAttributes };
  };

  return {
    fetch: useQuery(['homeAtrributes'], () => fetchData(), {
      refetchOnWindowFocus: false,
    }),
  };
};

export default useHomeAttributes;
